import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";

import { CenterBranchesComponent } from "./pages/users/center-branches/center-branches.component";
import { FunctionsSetupComponent } from "./pages/users/functions-setup/functions-setup.component";
import { ParentsChildrenComponent } from "./pages/users/parents/parents-children/parents-children.component";
import { ParentsIndexComponent } from "./pages/users/parents/parents-index/parents-index.component";
import { AddRoleComponent } from "./pages/users/roles/add-role/add-role.component";
import { RoleFunctionsComponent } from "./pages/users/roles/role-functions/role-functions.component";
import { AddTeacherSubjectsComponent } from "./pages/users/teachers/add-teacher-subjects/add-teacher-subjects.component";
import { TeachersIndexComponent } from "./pages/users/teachers/teachers-index/teachers-index.component";
import { UserBranchesRolesComponent } from "./pages/users/user-branches-roles/user-branches-roles.component";
import { UsersIndexComponent } from "./pages/users/users-add-index/users-add-index.component";

const routes: Routes = [
  {
    path: "",
    redirectTo: "users-index",
    pathMatch: "full",
  },
  {
    path: "users-index",
    component: UsersIndexComponent,
  },

  {
    path: "user-roles-branches/:userId/:roleId",
    component: UserBranchesRolesComponent,
  },

  {
    path: "functions",
    component: FunctionsSetupComponent,
  },

  {
    path: "center-branches",
    component: CenterBranchesComponent,
  },

  {
    path: "roles",
    component: AddRoleComponent,
  },
  {
    path: "role-fuctions/:roleId",
    component: RoleFunctionsComponent,
  },

  {
    path: "parents",
    children: [
      { path: "", redirectTo: "index", pathMatch: "full" },
      {
        path: "index",
        component: ParentsIndexComponent,
      },
      {
        path: "parent-children/:parentId",
        component: ParentsChildrenComponent,
      },
    ],
  },

  {
    path: "teachers",
    children: [
      { path: "", redirectTo: "index", pathMatch: "full" },
      {
        path: "index",
        component: TeachersIndexComponent,
      },
      {
        path: "teacher-subjects/:teacherId",
        component: AddTeacherSubjectsComponent,
      },
    ],
  },

  // {
  //   path: "setup",
  //   loadChildren: () =>
  //     import("./pages/setup/setup.module").then((m) => m.SetupModule),
  // },

  {
    path: "educational-setup",
    loadChildren: () =>
      import("./pages/setup/educational-setup/educational-setup.module").then(
        (m) => m.EducationalSetupModule
      ),
  },

  {
    path: "general-setup",
    loadChildren: () =>
      import("./pages/setup/general-setup/general-setup.module").then(
        (m) => m.GeneralSetupModule
      ),
  },

  {
    path: "financial-setup",
    loadChildren: () =>
      import("./pages/setup/financial-setup/financial-setup.module").then(
        (m) => m.FinancialSetupModule
      ),
  },
  {
    path: "curricula-setup",
    loadChildren: () =>
      import("./pages/setup/curricula-setup/curricula-setup.module").then(
        (m) => m.CurriculaSetupModule
      ),
  },

  {
    path: "students",
    loadChildren: () =>
      import("./pages/setup/student/student-application.module").then(
        (m) => m.StudentApplicationModule
      ),
  },
  {
    path: "financial",
    loadChildren: () =>
      import("./pages/Financial/financial.module").then(
        (m) => m.FinancialModule
      ),
  },

  {
    path: "exams-setup",
    loadChildren: () =>
      import("./pages/setup/exams-setup/exams.module").then(
        (m) => m.ExamsModule
      ),
  },

  /* employees */
  {
    path: "employees",
    loadChildren: () =>
      import("./pages/employees/employees.module").then(
        (m) => m.EmployeesModule
      ),
  },

  /* educational tabels */
  {
    path: "educational-tabels",
    loadChildren: () =>
      import("./pages/educational-tabels/educational-tabels.module").then(
        (m) => m.EducationalTabelsModule
      ),
  },
  /*control*/
  {
    path: "control",
    loadChildren: () =>
      import("./pages/control/control.module").then((m) => m.ControlModule),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class DashboardRoutingModule {}
