import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class RolesService {
  constructor(private http: HttpClient) {}

  getAllRoles<T>(EduCompId :number) {
    return this.http.get<T>(environment.baseUrl + "usermanagement/returnRoles"+"/"+EduCompId);
  }

  addEditRole<T, TV>(obj: T) {
    return this.http.post<TV>(
      `${environment.baseUrl}usermanagement/create-role`,
      obj
    );
  }

  deleteRole(roleId: number) {
    return this.http.delete(
      environment.baseUrl + "usermanagement/deleteRole" + "/" + roleId
    );
  }

  getAllFunctions<T>(page: number, getAll: boolean) {
    return this.http.get<T>(
      `${environment.baseUrl}usermanagement/getAllFunctions/${page}/${getAll}`
    );
  }

  getAllOwnedReports<T>() {
    return this.http.get<T>(
      `${environment.baseUrl}Admin/get-all-reports-index`
    );
  }
  getReportsByRoleId<T>(roleId: number) {
    return this.http.get<T>(
      `${environment.baseUrl}Admin/get-all-reports-by-roleId/${roleId}`
    );
  }


  getAllReports<T>() {
    return this.http.get<T>(
      `${environment.baseUrl}Admin/get-all-reports`
    );
  }

  getAllRoleFunctions<T>(roleId: number) {
    return this.http.get<T>(
      `${environment.baseUrl}usermanagement/returnRoleFunctionsByRoleID/${roleId}`
    );
  }

  addEditRoleFunctions<T, TV>(obj: T) {
    return this.http.post<TV>(
      environment.baseUrl + "usermanagement/add-function-to-role",
      obj
    );
  }

  getAllFunctionsByRoleId<T>(roleId: number) {
    return this.http.get<T>(
      `${environment.baseUrl}usermanagement/returnRoleFunctionsByRoleID/${roleId}`
    );
  }

  getAllUserBranchesWithFunctions<T>(userId: number) {
    return this.http.get<T>(
      `${environment.baseUrl}usermanagement/returnAllBranchUserAllRoleFunction/${userId}`
    );
  }

  addUserBranchesAndRoleFunctions<T, TV>(obj: T) {
    return this.http.post<TV>(
      environment.baseUrl + "usermanagement/addUserBranchesAndRoleFunctions",
      obj
    );
  }

  deleteUserRoleFunctions(functionId: number) {
    return this.http.delete(
      `${environment.baseUrl}usermanagement/deleteBranchUserRoleFunction/${functionId}`
    );
  }

  deleteUserBranchRoleFunction(userId: number, branchId: number) {
    return this.http.delete(
      `${environment.baseUrl}usermanagement/deleteBranchUserAllRoleFunction/${userId}/${branchId}`
    );
  }
}
