import { IChildrenListVM } from "app/dashboard/models/users/parents";
import { ParentChildService } from "app/dashboard/services/users/parent-child.service";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { IChildAddVM, IChildVM } from "app/dashboard/models/users/parents";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { MessagesService } from "app/shared/services/messages.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-parents-children",
  templateUrl: "./parents-children.component.html",
  styleUrls: ["./parents-children.component.scss"],
})
export class ParentsChildrenComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  itemsCounts: number = 0;
  curLang: string;
  EduCompId: number;
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  parentId: number;

  displayColumns: string[] = ["#", "name", "email", "EduYear", "actions"];
  dataSource: MatTableDataSource<IChildVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private ParentChildService: ParentChildService,
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    private dialog: MatDialog,
    private translate: TranslateService,
    public authService: AuthService,
    private route: ActivatedRoute,
    private fb: FormBuilder
  ) {
    route.paramMap.subscribe((params) => {
      this.parentId = Number(params.get("parentId"));
    });

    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
            }
          });
      }
    });
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.getParentChildren(1);

    this.myForm = this.fb.group({
      name: [
        "",
        [
          Validators.required,
          Validators.pattern(
            /^[a-zA-Z \u0600-\u065F\u066A-\u06EF\u06FA-\u06FF]+$/
          ),
          CustomeValidator.whiteSpace,
        ],
      ],
      eduYearId: ["", [Validators.required]],
      email: ["", [Validators.required, Validators.email]],
    });
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getParentChildren(1);
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  getParentChildren(page: number) {
    this.dataSource = new MatTableDataSource([
      {
        Id: 1,
        email: "dds@ff.cc",
        name: "hh",
        eduYearId: 1,
        parentId: 2,
        eduYear_ar_name: "Ss",
        eduYear_en_name: "dfds",
      },
    ]);
    this.spinner.show();
    this.ParentChildService.getParentChildren(page).subscribe(
      (res: IChildrenListVM) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res.list);
          this.itemsCounts = res.countItems;
          this.dataSource.sort = this.sort;
        }
        this.spinner.hide();
      }
    );
  }

  onSubmit() {
    this.submitted = true;

    let parent: IChildAddVM = {
      parentId: this.parentId,
      name: this.FormCtrls.name.value,
      email: this.FormCtrls.email.value,
      eduYearId: this.FormCtrls.eduYearId.value,
    };

    if (this.myForm.valid) {
      this.spinner.show();
      this.ParentChildService.AssignChildToParent(parent).subscribe(
        (res: any) => {
          if (res.returnValue == 1) {
            this.dataSource.data.unshift(res);
            this.itemsCounts++;
            this.dataSource.sort = this.sort;
            this.toastr.success("General.AddSuccessfully");
            this.clearForm();
          }
          if (res.returnValue == 0) this.toastr.error(res.returnString);
          this.spinner.hide();
        }
      );
    } else this.toastr.error("General.FormNotValid");
  }

  openDeleateDialog(parent: IChildVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: parent.name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteParent(parent);
      });
  }

  // Start:Delete Stage
  deleteParent(parent: IChildVM) {
    this.ParentChildService.deleteChild(parent.parentId, parent.Id).subscribe(
      (res: any) => {
        if (res.returnValue == 1) {
          let index = this.dataSource.data.findIndex((e) => e == parent);
          this.dataSource.data.splice(index, 1);
          this.dataSource.sort = this.sort;

          if (parent.parentId == this.FormCtrls.id.value) this.clearForm();

          this.toastr.success("General.DeleteSuccessfully");
        } else {
          this.toastr.error("General.TosterErrorMessage");
        }
      }
    );
  }

  resendEmail(parent: IChildVM) {
    this.spinner.show();
    this.ParentChildService.resendEmail(parent.parentId, parent.Id).subscribe(
      (res: any) => {
        if (res.returnValue == 1)
          this.toastr.success("General.ResendSuccessfully");
        else this.toastr.error("General.TosterErrorMessage");
        this.spinner.hide();
      }
    );
  }

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.Id.setValue(0);
    this.submitted = false;
  }
}
