import { TeacherService } from "app/dashboard/services/users/teacher.service";
import { Component, OnInit, ViewChild } from "@angular/core";
import {
  AbstractControlOptions,
  FormBuilder,
  FormGroup,
  Validators,
} from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  ISubjectTeacherAddVM,
  ISubjectTeacherListVM,
  ISubjectTeacherVM,
} from "app/dashboard/models/users/teacher";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { MessagesService } from "app/shared/services/messages.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-add-teacher-subjects",
  templateUrl: "./add-teacher-subjects.component.html",
  styleUrls: ["./add-teacher-subjects.component.scss"],
})
export class AddTeacherSubjectsComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  itemsCounts: number = 0;
  curLang: string;
  EduCompId: number;
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  teacherId: number;

  displayColumns: string[] = ["#", "EduYear", "Course", "actions"];
  dataSource: MatTableDataSource<ISubjectTeacherVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private TeacherService: TeacherService,
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    private dialog: MatDialog,
    private translate: TranslateService,
    public authService: AuthService,
    private route: ActivatedRoute,
    private fb: FormBuilder
  ) {
    route.paramMap.subscribe((params) => {
      this.teacherId = Number(params.get("teacherId"));
    });

    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
            }
          });
      }
    });

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });

    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.getTeacherCourses(1);

    this.myForm = this.fb.group({
      EduYear: ["", [Validators.required]],
      courseIds: ["", [Validators.required]],
    });
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getTeacherCourses(1);
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  getTeacherCourses(page: number) {
    this.dataSource = new MatTableDataSource([
      {
        courseId: 1,
        course_ar_name: "dd",
        course_en_name: "Aa",
        eduYearId: 2,
        eduYear_ar_name: "bjh",
        eduYear_en_name: "gvgb",
        Id: 1,
        teacherId: 3,
      },
    ]);
    this.spinner.show();
    this.TeacherService.getAllTeacherCourses(page).subscribe(
      (res: ISubjectTeacherListVM) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res.list);
          this.itemsCounts = res.countItems;
          this.dataSource.sort = this.sort;
        }
        this.spinner.hide();
      }
    );
  }

  getTeacherToEdit(teacher: ISubjectTeacherVM) {
    this.myForm.patchValue(teacher);
  }

  onSubmit() {
    this.submitted = true;

    let teacher: ISubjectTeacherAddVM = {
      courseId: this.FormCtrls.courseId.value,
      eduYearId: this.FormCtrls.eduYearId.value,
      teacherId: this.teacherId,
    };

    if (this.myForm.valid) {
      this.spinner.show();
      this.TeacherService.addTeacherCourse(teacher).subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.dataSource.data.unshift(res);
          this.itemsCounts++;

          this.dataSource.sort = this.sort;
          this.toastr.success("General.AddSuccessfully");
          this.clearForm();
        }
        if (res.returnValue == 0) {
          this.toastr.error(res.returnString);
        }
        this.spinner.hide();
      });
    } else this.toastr.error("General.FormNotValid");
  }

  openDeleateDialog(teacher: ISubjectTeacherVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg:
            this.curLang == "ar"
              ? teacher.course_ar_name
              : teacher.course_en_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteTeacher(teacher);
      });
  }

  // Start:Delete Stage
  deleteTeacher(teacher: ISubjectTeacherVM) {
    this.TeacherService.deleteTeacherCourse(teacher.Id).subscribe(
      (res: any) => {
        if (res.returnValue == 1) {
          let index = this.dataSource.data.findIndex((e) => e == teacher);
          this.dataSource.data.splice(index, 1);
          this.dataSource.sort = this.sort;

          if (teacher.Id == this.FormCtrls.id.value) this.clearForm();

          this.toastr.success("General.DeleteSuccessfully");
        } else {
          this.toastr.error("General.TosterErrorMessage");
        }
      }
    );
  }

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.Id.setValue(0);
    this.submitted = false;
  }
}
