import { environment } from "environments/environment";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  AddParentModel,
  IChildAddVM,
} from "app/dashboard/models/users/parents";

@Injectable({
  providedIn: "root",
})
export class ParentChildService {
  constructor(private http: HttpClient) {}

  addUpdateParent(body: AddParentModel) {
    return this.http.post(`${environment.baseUrl}Parent/RegisterParent`, body);
  }

  getParents(page: number) {
    return this.http.get(`${environment.baseUrl}Parent/return-all/${page}`);
  }

  getParentById(parentId: number) {
    return this.http.get(
      `${environment.baseUrl}Parent/GetParentById/${parentId}`
    );
  }

  deleteParent(parentId: number) {
    return this.http.delete(
      `${environment.baseUrl}Parent/DeleteParentById/${parentId}`
    );
  }

  getParentChildren(parentId: number) {
    return this.http.get(
      `${environment.baseUrl}Parent/return-children-by-parent-id/${parentId}`
    );
  }

  AssignChildToParent(body: IChildAddVM) {
    return this.http.post(
      `${environment.baseUrl}child/register-child-by-parentId`,
      body
    );
  }

  updateChild(body: any) {
    return this.http.post(`${environment.baseUrl}child/update-child`, body);
  }

  getChildData(childId: number) {
    return this.http.get(
      `${environment.baseUrl}child/get-child-data-by-parent-child-Id/${childId}`
    );
  }

  deleteChild(parentId: number, childId: number) {
    return this.http.delete(
      `${environment.baseUrl}Parent/DeleteChildrenByParentId/${parentId}/${childId}`
    );
  }

  resendEmail(parentId: number, childId: number) {
    return this.http.get(
      `${environment.baseUrl}child/resend-email-by-parentUserId-childId/${parentId}/${childId}`
    );
  }
}
