import { CommonModule } from "@angular/common";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { SharedModule } from "./../shared/shared.module";
import { NgModule } from "@angular/core";
import { MaterialModule } from "app/shared/material/material.module";
import { DashboardRoutingModule } from "./dashboard-routing.module";
import { NgxSpinnerModule } from "ngx-spinner";
import { NgbModule } from "@ng-bootstrap/ng-bootstrap";
import { MatPaginatorStyleDirective } from "app/shared/directives/mat-paginator-style.directive";
import { CenterBranchesComponent } from "./pages/users/center-branches/center-branches.component";
import { FunctionsSetupComponent } from "./pages/users/functions-setup/functions-setup.component";
import { AddRoleComponent } from "./pages/users/roles/add-role/add-role.component";
import { RoleFunctionsComponent } from "./pages/users/roles/role-functions/role-functions.component";
import { UserBranchesRolesComponent } from "./pages/users/user-branches-roles/user-branches-roles.component";
import { DateAdapter, MAT_DATE_FORMATS } from "@angular/material/core";
import {
  AppDateAdapter,
  APP_DATE_FORMATS,
} from "app/shared/validators/date.adapter";
import { ParentsIndexComponent } from "./pages/users/parents/parents-index/parents-index.component";
import { ParentsChildrenComponent } from "./pages/users/parents/parents-children/parents-children.component";
import { UsersIndexComponent } from "./pages/users/users-add-index/users-add-index.component";
import { TeachersIndexComponent } from './pages/users/teachers/teachers-index/teachers-index.component';
import { AddTeacherSubjectsComponent } from './pages/users/teachers/add-teacher-subjects/add-teacher-subjects.component';

@NgModule({
  imports: [
    CommonModule,
    DashboardRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    SharedModule,
    MaterialModule,
    NgbModule,
    NgxSpinnerModule,
  ],
  declarations: [
    UsersIndexComponent,
    UserBranchesRolesComponent,
    FunctionsSetupComponent,
    CenterBranchesComponent,
    MatPaginatorStyleDirective,
    AddRoleComponent,
    RoleFunctionsComponent,
    ParentsIndexComponent,
    ParentsChildrenComponent,
    TeachersIndexComponent,
    AddTeacherSubjectsComponent,

  ],
  providers: [
    {
      provide: DateAdapter,
      useClass: AppDateAdapter,
    },
    {
      provide: MAT_DATE_FORMATS,
      useValue: APP_DATE_FORMATS,
    },
  ],
})
export class DashboardModule {}
