import { ParentChildService } from "app/dashboard/services/users/parent-child.service";
import { Component, OnInit, ViewChild } from "@angular/core";
import {
  AbstractControlOptions,
  FormBuilder,
  FormGroup,
  Validators,
} from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  AddParentModel,
  IParentVM,
  ParentModel,
} from "app/dashboard/models/users/parents";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { MessagesService } from "app/shared/services/messages.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-parents-index",
  templateUrl: "./parents-index.component.html",
  styleUrls: ["./parents-index.component.scss"],
})
export class ParentsIndexComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  itemsCounts: number = 0;
  curLang: string;
  EduCompId: number;
  functionId: number;
  rowFunctions: IRowFunctionVM[];

  displayColumns: string[] = ["#", "name", "email", "mobile", "actions"];
  dataSource: MatTableDataSource<ParentModel>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private ParentChildService: ParentChildService,
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    private dialog: MatDialog,
    private translate: TranslateService,
    public authService: AuthService,
    private route: ActivatedRoute,
    private fb: FormBuilder
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
            }
          });
      }
    });
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.getParents(1);

    this.myForm = this.fb.group(
      {
        Id: [0],
        name: [
          "",
          [
            Validators.required,
            Validators.pattern(
              /^[a-zA-Z \u0600-\u065F\u066A-\u06EF\u06FA-\u06FF]+$/
            ),
            CustomeValidator.whiteSpace,
          ],
        ],
        mobile: [
          "",
          [
            Validators.required,
            CustomeValidator.whiteSpace,
            Validators.pattern(/^(\(?\+?[0-9]*\)?)?[0-9_\- \(\)]*$/),
            // Validators.pattern(/^(010|011|012|015)[0-9]{8}$/),
            Validators.maxLength(11),
            Validators.minLength(11),
            CustomeValidator.startsWith,
          ],
        ],
        email: ["", [Validators.required, Validators.email]],
        account_password: [
          "",
          [Validators.required, CustomeValidator.whiteSpace],
        ],
        confPassword: ["", [Validators.required]],
      },
      {
        validator: CustomeValidator.mustMatch(
          "account_password",
          "confPassword"
        ),
      } as AbstractControlOptions
    );
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getParents(1);
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  getParents(page: number) {
    this.dataSource = new MatTableDataSource([
      {
        email: "gg@dd.nn",
        mobile: "01258541256",
        name: "gg",
        userId: 1,
        parentId: 50,
      },
    ]);
    this.spinner.show();
    this.ParentChildService.getParents(page).subscribe((res: IParentVM) => {
      if (res) {
        this.dataSource = new MatTableDataSource(res.list);
        this.itemsCounts = res.countItems;
        this.dataSource.sort = this.sort;
      }
      this.spinner.hide();
    });
  }

  getParentToEdit(parent: ParentModel) {
    this.myForm.patchValue(parent);
    this.FormCtrls.Id.setValue(parent.parentId);
  }

  onSubmit() {
    this.submitted = true;

    let parent: AddParentModel = {
      Id: this.FormCtrls.Id.value,
      name: this.FormCtrls.name.value,
      userTypeId: 3,
      mobile: this.FormCtrls.mobile.value,
      email: this.FormCtrls.email.value,
      account_password: this.FormCtrls.account_password.value,
      confPassword: this.FormCtrls.confPassword.value,
    };

    if (this.myForm.valid) {
      this.spinner.show();
      this.ParentChildService.addUpdateParent(parent).subscribe((res: any) => {
        if (res.returnValue == 1) {
          if (parent.Id == 0) {
            this.dataSource.data.unshift(res);
            this.itemsCounts++;
          } else {
            let index = this.dataSource.data.findIndex(
              (e) => e.userId == res.userId
            );
            this.dataSource.data[index] = res;
          }
          this.dataSource.sort = this.sort;
          this.toastr.success(
            res.id == 0
              ? "General.AddSuccessfully"
              : "General.UpdateSuccessfully"
          );
          this.clearForm();
        }
        if (res.returnValue == 0) {
          this.toastr.error(res.returnString);
        }
        this.spinner.hide();
      });
    } else this.toastr.error("General.FormNotValid");
  }

  // updateUser() {
  //   this.submitted = true;
  //   let valid = this.myForm.valid;
  //   if (valid) {
  //     this.spinner.show();
  //     this.GeneralService.updateUser(
  //       this.FormCtrl.Id.value,
  //       this.myForm.value
  //     ).subscribe((res: any) => {
  //       if (res.returnValue == 1) {
  //         this.myForm.reset();
  //         this.submitted = false;
  //         this.getParents();
  //         this.toastr.success("Updated");
  //       }
  //       if (res.returnValue == 0) {
  //         this.toastr.error(res.returnString);
  //       }
  //       this.spinner.hide();
  //     });
  //   }
  // }

  openDeleateDialog(parent: ParentModel): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: parent.name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteParent(parent);
      });
  }

  // Start:Delete Stage
  deleteParent(parent: ParentModel) {
    this.ParentChildService.deleteParent(parent.parentId).subscribe(
      (res: any) => {
        if (res.returnValue == 1) {
          let index = this.dataSource.data.findIndex((e) => e == parent);
          this.dataSource.data.splice(index, 1);
          this.dataSource.sort = this.sort;

          if (parent.parentId == this.FormCtrls.id.value) this.clearForm();

          this.toastr.success("General.DeleteSuccessfully");
        } else {
          this.toastr.error("General.TosterErrorMessage");
        }
      }
    );
  }

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.Id.setValue(0);
    this.submitted = false;
  }
}
